@import url(https://fonts.googleapis.com/css2?family=Bitter:wght@700;900&family=Work+Sans&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 100vw;
  overflow-x: hidden;
}




:root{
  --primary-ff: 'Bitter',serif;
  --secondary-ff:'Work Sans',sans-serif;
  --primary-clr: rgb(255, 255, 255);
  --secondary-clr:   #080808;
  --secondary-clr-1: #61fa36;
}

h1,h2,h3,h4,h5,h6{
  font-family: rgb(255, 255, 255);
  font-family: var(--primary-clr);
}
.App {
  text-align: center;
  max-width: 100vw;
  overflow-x: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

header{
  width: 100%;
  height: 65px;
  background: var(--secondary-clr);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 2px 0px;
  font-family: var(--primary-ff);
  color: var(--primary-clr);
  /* border-bottom: 5px solid var(--primary-clr); */
}

a{
  text-decoration: none;
}

.nav-links li:hover{
  color:var(--secondary-clr-1)
}

a h1{
  margin: auto auto auto 10px;
  color: var(--secondary-clr-1);
}

.nav-icon:hover{
  color: var(--secondary-clr-1);
}
.nav-icon{
  margin: auto 20px auto auto;
  font-size: 2rem;
  cursor: pointer;
  text-align: center;
}



.nav-links{
  list-style: none;
  position: absolute;
  width: 100%;
  height:  calc(100vh - 60px);
  max-width: 100%;
  padding: 0;
  top: 60px;
  left: 0;
  text-align: center;
  background: var(--secondary-clr);
  /* border-bottom: 6px solid var(--primary-clr); */
  z-index: 99;
}

.nav-links li{
  margin-top: 2rem;
  font-size: 1.5rem;
  color: var(--primary-clr);
}

.toggle{
  display: none;
}

@media (min-width:786px){

  header{
    display: flex;
    justify-content: space-between;
    align-items: center;
   padding-left: 50px;
  }

  .nav-links{
    position: relative;
    background: inherit;
    max-width: 500px;
    top: 0%;
    border-bottom: none;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-right: 40px;
  }

  .nav-links li{
    margin-top: 0;
    font-size: 1rem;
    font-weight: lighter;
  }

  .nav-icon{
    display: none;
  }
}
main{
  width: 100%;
  height: auto;
  max-width: 100%;

  
  background-color: rgba(255, 255, 255, 0.829);
}
.homepage{
  width: 100%;
}

.homepage-hero{
  background:linear-gradient(rgba(0, 0, 0, 0.356),rgba(0, 0, 0, 0.329)) ,url('https://images.unsplash.com/photo-1603357465999-241beecc2629?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1404&q=80') center/cover no-repeat;
  height: 90vh;
  display: flex;
  align-items: center;
  color: aliceblue;
}

.homepage-text{
  text-align: left;
  max-width: 400px;
  margin-left: 1rem;
}



.homepage-head{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  max-width: 340px;
  color: var(--secondary-clr-1);
  text-transform: uppercase;margin-bottom: 1rem;
  flex-wrap: nowrap;
}

.homepage-text h1{
  font-family: var(--primary-ff);
  margin-bottom: 1.6rem;
  font-size: 3rem;
}

.homepage-head p{
  font-family: var(--secondary-ff);  
  text-transform: uppercase;
  font-size: 1.2rem;
  font-weight: 200;
}

.homepage-head-link{
  display: inline-block;
  margin-top: 1rem;
  text-transform: capitalize;
  text-align: center;
  padding: 15px 25px;
  text-transform: uppercase;
  border-radius: 6px;
  background: var(--secondary-clr-1);
  color: aliceblue;
  position: absolute;
  font-size: 1.4rem;
  font-weight: 500;
}

.homepage-head-link:hover{
  background: black;
  transition: background 0.4s ease;
}

.homepage-head-underline{
  width: 80px;
  height: 3px;
  border-radius: 3px;
  background: var(--secondary-clr-1);
}


@media (min-width:786px){
  .homepage-text{
    max-width: 500px;
  }

   .homepage-text h1{
    font-size: 4rem;
  }

  .homepage-head{
    max-width: 400px;
  }

  .homepage-head > p{
    font-size: 1.4rem;
  }
}



